#about {
  width: 80%;
  margin: 100px auto;
}

.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about__container p {
  text-align: left;
}


.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
  display: grid;
  place-items: center;
}

.about__me-image {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition: var(--transition);
}

.about__me-image:hover {
  transform: rotate(0);
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

.about__card {
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
}

.about__card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.about__icon {
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.about__card h5 {
  font-size: 0.95rem;
}

.about__card small {
  font-size: 0.7rem;
  color: var(--color-light);
}

/* ======================= MEDIA QUERIES (MEDIUM DEVICES) ============================ */

@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about__content p {
    margin: 1rem 0 1.5rem;
  }
}

/* ======================= MEDIA QUERIES (SMALL DEVICES) ============================ */

@media screen and (max-width: 600px) {
  #about {
    margin-top: 15em;
  }

  .about__me {
    display: none;
  }

  .about__cards {
    display: flex;
    flex-flow: column wrap;
  }

  .about__card {
    width: 100%;
    margin: 0 auto;
  }

  .about__content {
    text-align: center;
  }

  .about__container {
    display: flex;
    flex-flow: column wrap;
  }

  .about__container p {
    text-align: center;
    width: 100%;
    margin: 0 auto;
  }
}